<template>
	<div>
		<div class="content-title">Dodaj nowe wydarzenie</div>
		<div class="row">
			<div class="col-md-8">
				<div class="card">
					<div class="card-content">
						<form>
							<table style="width: 100%;">
								<tr>
									<td colspan="2"><input type="text" placeholder="Tytu" v-model="form.name" required/></td>
								</tr>
								<tr>
									<td colspan="2"><input type="text" placeholder="Krtka tre notyfikacji" v-model="form.message" required/></td>
								</tr> 
								<tr>
									<td colspan="2"><input type="url" placeholder="Link do obrazka opisujcego" v-model="form.image"/></td>
								</tr>
								<tr>
									<td colspan="2"><textarea placeholder="Pena tre komunikatu" v-model="form.description" required></textarea></td>
								</tr> 
								<tr>
								<tr>
									<td>
										<select>
											<option value="0">Informacja oglna</option>
											<option value="1">Komunikat spersonalizowany</option>
											<option value="2">Bardzo wany komunikat</option>
										</select>
									</td>
									<td class="text-right"><button @click.prevent="addEvent">Zakocz dodawanie</button></td>
								</tr>
							</table>
						</form>
                    </div>
                </div>
			</div>
			<div class="col-md-4">
                <div class="card">
					<div class="card-title">Podgld</div>
					<div class="card-content">
						Podgld wiadomoci na messengerze<br/>
						prv: <strong>{{ form.message }}</strong><br/>
						Podgld maila<br/>
                        Podgld wiadomoci SMS<br/>
                    </div>
                </div>            
            </div>
		</div>
	</div>
</template>

<script>
	import auth from '../../../auth'
	import router from '../../../router'

	export default {
		data() {
			return {
				form: {
					name: '',
					description: '',
					image: '',
					message: '',
					group: ''
				}
			}
		},
		methods: {
			addEvent: function() {
				this.$http.post('https://uek.maciekmm.net/events/', { 
					priority: 0, 
					name: this.form.name, 
					image: this.form.image, 
					message: this.form.message, 
					description: this.form.description 
				}, { headers: auth.getAuthHeader() }).then(data => {

					router.push({ name: 'events' })
					
				}, data => {
					console.log(data.err)
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	input[type=text], input[type=url] {
		width: 100%;	
	}
	textarea {
		width: 100%;
		min-height: 200px;
		resize: vertical;
	}
</style>



